<template>
  <div id="registration">
    <form>
      <md-card id="registration-card">
        <md-card-header>
          <div class="md-title ">{{ $i('sign.up.title') }}</div>
        </md-card-header>
        <md-card-content id="card-content">
          <md-field :class="getValidationClass('email')">
            <label>{{ $i('email') }}</label>
            <md-input name="email" v-model="user.email" @input="$v.user.email.$touch()"></md-input>
            <span class="md-error" v-if="!$v.user.email.required"> {{ $i('validation.required') }}</span>
            <span class="md-error" v-else-if="!$v.user.email.email"> {{ $i('validation.email') }} </span>
          </md-field>

          <md-field :class="getValidationClass('password')">
            <label>{{ $i('password') }}</label>
            <md-input v-model="user.password" type="password" @input="$v.user.password.$touch()"></md-input>
            <span class="md-error" v-if="!$v.user.email.required"> {{ $i('validation.required') }}</span>
            <span class="md-error" v-else-if="!$v.user.email.password"> {{ $i('validation.password') }} </span>
          </md-field>
        </md-card-content>
        <md-button class="md-primary" @click="registration()">{{ $i('sign-up') }}</md-button>
      </md-card>
    </form>
  </div>
</template>


<script>
import User from '../../model/user';
import { validationMixin } from 'vuelidate'
import { required, email, helpers} from 'vuelidate/lib/validators'
const passwordPattern = helpers.regex('passwordPattern', /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$/)

export default {
  name: "RegistrationComponent",
  data() {
    return {
      user: new User()
    }
  },
  validations: {
    user: {
      email: {
        required,
        email
      },
      password: {
        required,
        passwordPattern
      }
    },
  },
  methods: {
    registration: function () {
      this.$actimate.post('/api/registration', this.user)
        .then(this.handleSuccessRegistration)
        .catch(this.handleErrorRegistration)
    },
    getValidationClass (fieldName) {
      const field = this.$v.user[fieldName]
      if (field) {
        return {
          'md-invalid': field.$invalid && field.$dirty
        }
      }
    },
    handleSuccessRegistration: function() {
      this.$emit('success')
    },
    handleErrorRegistration: function() {
      this.$emit('error')
    }
  },
  mixins: [validationMixin]
}
</script>

<style scoped>
#registration {
  /*margin-top: 80px;*/
  text-align: center;
  /*margin: 80px;*/
}

#registration-card {
  position: relative;
  max-width: 50%;
  margin: auto;
}

#card-content {
  margin-bottom: -20px;
}
</style>



