<template>
  <div id="root">
    <page-root></page-root>
    <!--<notifications-root></notifications-root>-->
    <notifications></notifications>

  </div>
</template>

<script>
export default {
  name: 'Root',
  methods: {
    addNotification: function(message) {
      this.$notify({
        message: message,
        type: 'success'
      })
    }
  }
}
</script>

/* Put here style that is applied to all components */
<style src="vue-notifyjs/themes/default.css"></style>
<style>

.container {
  margin-top: 25px;
}

.growAppearAndCollapseFade-enter-active {
  animation: grow-and-appear 1.2s ease forwards;
}

.growAppearAndCollapseFade-leave-active {
  animation: collapse-and-fade 1.2s ease forwards;
}

@keyframes grow-and-appear {
  0% {
    opacity: 0;
    max-height: 0;
  }

  20% {
    opacity: 0;
  }

  60% {
    max-height: 25em;
  }

  100% {
    opacity: 1;
    max-height: 25em;
  }
}

@keyframes collapse-and-fade {
  0% {
    opacity: 1;
    max-height: 25em;
  }

  20% {
    max-height: 25em;
  }

  60% {
    opacity: 0;
  }

  100% {
    opacity: 0;
    max-height: 0;
  }
}
</style>



