<template>
<div class="container">
  <div id="footer">
    <div class="d-flex justify-content-between">
      <!--<div class="license">-->
        <!--{{ $i('footer.license') }}-->
      <!--</div>-->
      <div>
        <div class="brand">
          Actimate | 2019
        </div>
        <div class="language">
          <button class="lang-button" @click="$setLang('hu')">
            <img class="button-img" src="../../../static/images/hun.jpeg"/>
          </button>
          <button class="lang-button" @click="$setLang('eng')">
            <img class="button-img" src="../../../static/images/eng.jpeg"/>
          </button>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
export default {
    name: "FooterComponent"
}
</script>

<style scoped>
.container {
  margin: 0;
  padding: 0;
}

#footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  border-top: 1px solid #555;
  box-shadow: 0 0 30px 30px white;
  padding: 5px;
  color: #555;
  z-index: 10000;
  background-color: white;
}

.language {
  margin-right: 25px;
}

.lang-button {
  position: relative;
  overflow: hidden;
  height: 20px;
  padding: 0;
  border: 0;
  cursor: pointer;
}

.lang-button:focus {
  outline: 0;
}

.button-img {
  position: relative;
  top: -8px;
  width: 35px;
  height: 35px;
}
</style>



