<template>
<div id="contact-us">
  <form>
    <div>
      <md-card id="contact-us-card">
        <md-card-header>
          <div class="md-title ">{{ $i('contact.us.title') }}</div>
        </md-card-header>
        <md-card-content id="card-content">
          <md-field>
            <label>{{ $i('email') }}</label>
            <md-input v-model="email.email" required></md-input>
            <span class="md-error">The age is required</span>
          </md-field>

          <md-field>
            <label>{{ $i('sender') }}</label>
            <md-input v-model="email.sender"></md-input>
          </md-field>

          <md-field>
            <label>{{ $i('message') }}</label>
            <md-textarea required v-model="email.content"></md-textarea>
          </md-field>
        </md-card-content>
        <md-button class="md-primary" @click="sendEmail()">{{ $i('send') }}</md-button>
      </md-card>
    </div>
  </form>
</div>

</template>

<script>
import ContactMessage from "../../model/contact-message";
import { validationMixin } from 'vuelidate'
import { required, email } from 'vuelidate/lib/validators'

export default {
  name: "ContactUsComponent",
  data() {
    return {
      email: new ContactMessage
    }
  },
  validations: {
    email: {
      email: {
        required,
        email
      },
      content: {
        required
      },
      sender: {
        required
      }
    }
  },
  methods: {
    sendEmail: function () {
      console.log(this.email)
      this.$actimate.post('/api/contact-me', this.email)
        .then(this.handleSuccess)
        .catch(this.handleError)
    },
    handleSuccess:function() {
      this.$emit('success')
    },
    handleError:function(err) {
      // this.$emit('error', err)
      this.$emit('success')
    }
  },
  mixins: [validationMixin]
}
</script>

<style scoped>
#contact-us {
  background-image: url("https://images.pexels.com/photos/163444/sport-treadmill-tor-route-163444.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260");
  background-position: -200px;
  margin: 80px 0 80px 0;
  text-align: center;
}

#contact-us-card {
  position: relative;
  max-width: 50%;
  margin: auto;
}

#card-content {
  margin-bottom: -20px;
}

</style>



