<template lang="html">
<div>
  <navbar-component></navbar-component>
  <div class="md-medium-size-60">
    <div id="introduction" class="fancy-text">
      <h3 class="md-xsmall-size-25 md-small-size-45 home-page-text">{{ $i('home.title.text') }}</h3>
      <p class="md-xsmall-size-25 md-small-size-45 home-page-text">{{ $i('home.title.content') }}</p>
    </div>

    <div class="bgimg-1">
      <div id="sign-up-button-div" class="caption">
        <md-button id="sign-up-button" class="md-dense show-button" @click="fetchSignUp = true">{{ $i('sign.up') }}</md-button>
      </div>
    </div>
    <transition name="growAppearAndCollapseFade">
      <div v-if="fetchSignUp">
        <registration-component @success="success" @error="error"></registration-component>
      </div>
    </transition>

    <div class="fancy-text">
      <h3 class="md-xsmall-size-25 md-small-size-45 home-page-text">{{ $i('home.fancy.text') }}</h3>
      <p class="md-xsmall-size-25 md-small-size-45 home-page-text">{{ $i('home.fancy.content') }}</p>
    </div>

    <div class="bgimg-2">
      <div id="contact-us-button-div" class="caption">
        <md-button id="contact-us-button" class="md-dense show-button" @click="fetchContactUs = true">{{ $i('contact.us') }}</md-button>
      </div>
    </div>
    <transition name="growAppearAndCollapseFade">
      <div v-if="fetchContactUs">
        <contact-us-component @success="fetchContactUs = false"></contact-us-component>
      </div>
    </transition>

    <div class="fancy-text">
      <h3 class="md-xsmall-size-25 md-small-size-45 home-page-text">{{ $i('home.other.text') }}</h3>
      <p class="md-xsmall-size-25 md-small-size-45 home-page-text">{{ $i('home.other.content') }}</p>
    </div>


    <div class="bgimg-3"></div>

  </div>
  <footer-component></footer-component>
</div>
</template>

<script>
export default {
  name: "HomePage",
  data() {
    return {
      fetchSignUp: false,
      fetchContactUs: false
    }
  },
  methods: {
    success: function() {
      this.fetchSignUp = false
      this.$notify({
        message: this.$i('message'),
        type: 'success'
      })
    },
    error: function() {
      this.fetchSignUp = false
      this.$notify({
        message: this.$i('message'),
        type: 'danger'
      })
    }
  }
}
</script>

<style lang="css" scoped>
.show-button {
  background-color:transparent;
  font-size:50px;
}

h3 {
  font-size: medium;
}

p.home-page-text {
  word-wrap: break-word;
  max-width: 40%;
  margin: auto;
  font-size: medium;
}

.fancy-text {
  margin-bottom: 50px;
}

.bgimg-1, .bgimg-2, .bgimg-3 {
  position: relative;
  opacity: 0.75;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin: 50px 0 50px 0;
}
.bgimg-1 {
  background-image: url("https://images.pexels.com/photos/209977/pexels-photo-209977.jpeg");
  min-height: 450px;
}

.bgimg-2 {
  /*background-image: url("/static/images/2.jpg");*/
  background-image: url("https://images.pexels.com/photos/213966/pexels-photo-213966.jpeg");
  min-height: 400px;
}

.bgimg-3 {
  background-image: url("https://images.pexels.com/photos/274506/pexels-photo-274506.jpeg");
  min-height: 450px;
}

.caption {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  text-align: center;
  color: #000;
}

.md-button.md-dense {
  height: 50px;
}

h3 {
  letter-spacing: 5px;
  text-transform: uppercase;
  font: 20px "Lato", sans-serif;
  color: #111;
}

/* Turn off parallax scrolling for tablets and phones */
@media only screen and (max-device-width: 1024px) {
  .bgimg-1, .bgimg-2, .bgimg-3 {
    background-attachment: scroll;
  }
}

#contact-me {
}

.fancy-text {
  text-align: center;
}
</style>



